
import { getListTask,getDetailInfo} from '../../service/service.js'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import Flow from 'vux/src/components/flow/flow.vue'
import FlowState from 'vux/src/components/flow/flow-state.vue'
import FlowLine from 'vux/src/components/flow/flow-line.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

export default{
data(){
return{
infoList:[],
detailInfo:{},
show:false,
show1:false,
storyshow:true,
choiceShow:false,
bugshow:false,
confirmshow:false,
show2:false,
datashow:true,
nextshow:false,
resultshow:false,
resolvetshow:false,
acceptshow:false,
failshow:false,
btnshow:true,
taskTime:"",
remark:"",
list:[]
}
},
directives: {
TransferDom
},
components: {
Group,
Cell,
Datetime,
CellBox,
Confirm,
XInput ,
XTextarea ,
Flow,
FlowState,
FlowLine
},
methods:{
agree(){
this.confirmshow = true;
},
reject(){
this.confirmshow = true;
},
change(value){
console.log(value);
},
onCancel(){

},
onConfirm(){
console.log(this.taskTime);
console.log(this.remark);
}


},
created(){
var path = this.$route.path;
if(path.indexOf("finish")>0){
this.btnshow = false;
}
let token  = localStorage.getItem("token");
let code = this.$route.query.data;
let time = new Date().getTime();
getListTask(code,token).then((result)=>{
this.infoList = result;

})
getDetailInfo(time,token,code).then((result)=>{
if(result.length>=1){
this.detailInfo = JSON.parse(result[0].json_data);
}
var t = JSON.parse(result[0].json_data);
})

},
mounted(){
var self = this;
self.mescroll = new MeScroll("mescroll",{
up:{
isBounce:false,
use:false,
auto:false

},
down:{
use:false,
auto:false,
isLock:false
}
})



}

}




