
import {getTask} from '../../service/service.js'
import Search from 'vux/src/components/search/index.vue'
import Icon from 'vux/src/components/icon/index.vue'

export default{
data(){
return{
show:false,
listArr:[],
value:""
}
},
components: {
Search,
Icon
},
methods:{
goDetail(param){
this.$router.push({path:"/Rose/to_do/do_detail",query:{
data:param
}})
},
setFocus () {
this.$refs.search.setFocus()
},
resultClick (item) {
window.alert('you click the result item: ' + JSON.stringify(item))
},
getResult (val) {


},
onSubmit (value) {






console.log(value);
},
onFocus () {
console.log('on focus')
},
onCancel () {
console.log('on cancel')
},
upCallback(page) {
var self = this;
getListDataFromNet(page.num, function(curPageData) {
self.listArr = self.listArr.concat(curPageData);
self.mescroll.endSuccess(curPageData.length);

}, function() {

self.mescroll.endErr();
});
function getListDataFromNet(pageNum,successCallback,errorCallback){
setTimeout(function () {
var token = localStorage.getItem("token");
getTask(token,pageNum).then((res)=>{
successCallback&&successCallback(res.data.tableContent);
})


},500)
}


},
},
mounted(){
var self = this;
self.mescroll = new MeScroll("dmescroll",{
up:{
isBounce:false,
page:{
num:1,
size:10
},
callback: self.upCallback

},
down:{
use:false
}

})
},
created(){
var token = localStorage.getItem("token");
var page = 1;
getTask(token,page).then((res)=>{
this.listArr = res.data.tableContent;
this.$event.$emit("num",res.data.dataCount);

})


}


}





