
import { getLogin,getDoneTask } from '../../service/service.js'
import Search from 'vux/src/components/search/index.vue'
import Icon from 'vux/src/components/icon/index.vue'

export default{
data(){
return{
doneList:[],
mescroll:null,
show:true,
list:[],
value:""

}
},
components: {
Search,
Icon
},
methods:{
goDetail(param){
this.$router.push({path:"/Rose/finish/done_detail",query:{
data:param
}})
},
setFocus () {
this.$refs.search.setFocus()
},
resultClick (item) {
window.alert('you click the result item: ' + JSON.stringify(item))
},
getResult (val) {


},
onSubmit (value) {






console.log(value);
},
onFocus () {
console.log('on focus')
},
onCancel () {
console.log('on cancel')
},
upCallback(page) {
var self = this;
getListDataFromNet(page.num, function(curPageData) {
self.doneList = self.doneList.concat(curPageData);
self.mescroll.endSuccess(curPageData.length);
console.log(self.doneList);

}, function() {

self.mescroll.endErr();
});
function getListDataFromNet(pageNum,successCallback,errorCallback){
setTimeout(function () {
var token = localStorage.getItem("token");
getDoneTask(token,pageNum).then((res)=>{
successCallback&&successCallback(res.tableContent);
})


},500)
}


}



},
created(){
var token = localStorage.getItem("token");
var page =1;
getDoneTask(token,page).then((res)=>{
this.doneList = res.tableContent;
})

},
mounted(){
var self = this;
self.mescroll = new MeScroll("fmescroll",{
up:{
isBounce:false,
page:{
num:1,
size:10
},
callback: self.upCallback
},
down:{
use:false
}
})
}

}





